import React from "react"
import Title from "./title"

const Layout = ({ title, canonical, upperContent, lowerContent, classes=[] }) => {

  function getClasses(classes) {
    return "content--fillheight " + classes.join(" ");
  }

  return (
    <div className="container">
      <Title title={title} canonical={canonical}></Title>

      <div className={getClasses(classes)}>
        {upperContent}
      </div>
      {lowerContent}
    </div>
  )
}

export default Layout
