import React from "react"
import HeaderLogo from "./../images/header-logo-hor-6.png"
import { GoThreeBars } from 'react-icons/go';
import { GoX } from 'react-icons/go';
import { IconContext } from "react-icons";
import FooterLogoNavMobileImg from "../images/footer-logo-nav.png"
import { Link } from "gatsby"

class NavMobile extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    const isShown = this.props.isMenuExpanded ? "nav__mobile--shown" : "nav__mobile--hidden";
    const classes = `nav__mobile hide-lg ${isShown}`;

    return (
      <div className={classes}>
        <div className="navbar__close" onClick={this.props.toggleMenuExpand}>
          <IconContext.Provider value={{ className: "" }}>
            <GoX />
          </IconContext.Provider>
        </div>
        <ul className="nav__menu nav__menu-mobile">
          <li className="menu__item menu__item-mobile"><Link to="/"  className="menu__link menu__link-mobile menu__link-mobile--first">home</Link></li>
          <li className="menu__item menu__item-mobile"><a href="#" className="menu__link menu__link-mobile">practice areas</a></li>
          <li className="menu__item menu__item-mobile"><Link to="/our-attorneys/" className="menu__link menu__link-mobile">our attorneys</Link></li>
          <li className="menu__item menu__item-mobile"><a href="#" className="menu__link menu__link-mobile">contact</a></li>
        </ul>
        <div className="nav__mobile-logo">
          <img className="nav__mobile-logo-img" src={FooterLogoNavMobileImg} alt="Sopian Sitepu & Partners" />
        </div>
      </div>
    )
  }
}

class Navbar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isMenuExpanded: false
    }

    this.toggleBodyOverflow = this.toggleBodyOverflow.bind(this);
    this.toggleMenuExpand = this.toggleMenuExpand.bind(this);
  }

  toggleBodyOverflow() {
    if (this.state.isMenuExpanded) {
      document.body.classList.add('hamburger-open');
    } else {
      document.body.classList.remove('hamburger-open');
    }
  }

  toggleMenuExpand(event) {
    this.setState({ isMenuExpanded: !this.state.isMenuExpanded }, this.toggleBodyOverflow);
  }

  render() {
    const navbarLogoHiddenClass = this.state.isMenuExpanded ?  "navbar__logo--hidden" : "navbar__logo--shown";
    const navbarLogoClasses = `navbar__logo ${navbarLogoHiddenClass}`
    return (
      <div className="page__row navbar">
        <div className="row__wrapper navbar__wrapper">
          <div className={navbarLogoClasses}>
            <a href="" className="logo__link">
              <img src={HeaderLogo} alt="Sopian Sitepu and Partners" className="logo__img"></img>
            </a>
          </div>
          <div className="nav__desktop hide-sm hide-md">
            <ul className="nav__menu nav__menu-desktop">
              <li className="menu__item menu__item-desktop"><Link to="/" className="menu__link menu__link-desktop">home</Link></li>
              <li className="menu__item menu__item-desktop"><Link to="/practice-areas/" className="menu__link menu__link-desktop">practice areas</Link></li>
              <li className="menu__item menu__item-desktop"><Link to="/our-attorneys/" className="menu__link menu__link-desktop">our attorneys</Link></li>
              <li className="menu__item menu__item-desktop"><Link to="/contact/" className="menu__link menu__link-desktop">contact</Link></li>
            </ul>
          </div>
          <div className="navbar__call hide-sm">
            <span className="call__text"><strong>You Only Pay If We Win</strong></span>
            <a href="tel:02142880765" className="call__link"><strong>(021) 4288 0765</strong></a>
          </div>
          <div className="navbar__hamburger hide-lg" onClick={this.toggleMenuExpand}>
            <IconContext.Provider value={{ className: "hamburger__icon" }}>
              <GoThreeBars />
            </IconContext.Provider>
          </div>
        </div>
        <NavMobile isMenuExpanded={this.state.isMenuExpanded} toggleMenuExpand={this.toggleMenuExpand} />
      </div>
    )
  }
}

export default Navbar;