import React from "react";
import {Helmet} from "react-helmet";

class Title extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <Helmet>
            <meta charSet="utf-8" />
            <title>{this.props.title}</title>
            <link rel="canonical" href={this.props.canonical} />
          </Helmet>
        )
    }
}

export default Title